"use client"; // Marks this as a Client Component for React hooks usage

// Import React hooks: useState for state, useEffect for side effects (data fetching)
import { useState, useEffect } from "react";
// Import shared Navbar component
import Navbar from "../../components/Navbar";
// Import Next.js router for navigation
import { useRouter } from "next/navigation";

// Define TypeScript interface for Ticket object to ensure type safety
interface Ticket {
  id: number; // Unique identifier
  title: string; // Ticket subject
  description: string; // Detailed description
  priority: string; // Priority level (Low, Medium, High, Critical)
  category?: string; // Optional category (Concern, Problem, etc.)
  status: string; // Current status (Open, In Progress, Closed)
  createdBy: string; // Username of creator
  dept: string; // Department the ticket belongs to
  date: string; // ISO date string of creation
}

export default function TicketsPage() {
  const router = useRouter(); // Hook for programmatic navigation

  // State to store list of tickets fetched from backend
  const [tickets, setTickets] = useState<Ticket[]>([]);

  // State to store current logged-in user info
  const [user, setUser] = useState<any>(null);

  // Check muna natin kung naka-login si user pagkabukas ng page
  useEffect(() => {
    // Hanapin natin 'yung user data sa storage
    const storedUser = localStorage.getItem("user");
    if (storedUser) {
      // Pag nahanap, i-save natin sa state para magamit
      setUser(JSON.parse(storedUser));
    } else {
      // Pag wala, redirect natin sa login page
      router.push("/login");
    }
  }, [router]);

  // Kunin natin 'yung mga tickets galing sa backend
  const fetchTickets = async () => {
    try {
      // Tawagan natin 'yung tickets endpoint
      const res = await fetch("http://localhost:3001/api/tickets");
      if (res.ok) {
        // I-update na natin 'yung state gamit 'yung bagong data
        const data = await res.json();
        setTickets(data);
      }
    } catch (error) {
      console.error("Error fetching tickets:", error);
    }
  };

  // Load natin 'yung tickets kapag okay na 'yung user login
  useEffect(() => {
    if (user) {
      fetchTickets();
    }
  }, [user]);

  // Dito naman nag-uupdate ng status (pang Dept Head lang 'to)
  const handleStatusChange = async (ticketId: number, newStatus: string) => {
    try {
      // Send natin 'yung bagong status sa server
      const res = await fetch(`http://localhost:3001/api/tickets/${ticketId}`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ status: newStatus }),
      });

      if (res.ok) {
        // Refresh natin 'yung listahan para updated 'yung itsura
        fetchTickets();
      } else {
        alert("Failed to update status");
      }
    } catch (error) {
      console.error("Error updating status:", error);
    }
  };

  // Loading muna habang chinecheck pa natin 'yung login
  if (!user) {
    return (
      <div className="flex justify-center items-center h-screen bg-gray-50 text-gray-500 font-medium">
        Loading...
      </div>
    );
  }

  // Dito natin pinipili kung anong tickets lang ang dapat makita
  const filteredTickets = tickets.filter((ticket) => {
    if (user.role === "Head") {
      // Pag Dept Head, dapat makita niya lahat ng requests sa department niya
      return ticket.dept === user.dept;
    }
    // Pag regular User, 'yung mga ginawa lang niya ang lalabas
    return ticket.createdBy === user.username;
  });

  return (
    <div className="min-h-screen bg-gray-50 relative">
      {/* Navigation Bar */}
      <Navbar user={user} />

      {/* Main Content Area */}
      <main className="max-w-7xl mx-auto py-10 px-8 sm:px-12 lg:px-16">
        <div className="bg-white overflow-hidden shadow-sm sm:rounded-lg">
          <div className="p-8 bg-white border-b border-gray-200">
            {/* Header and Action Button */}
            <div className="flex justify-between items-center mb-6">
              <h2 className="text-2xl font-bold text-gray-900">
                Ticket Management
              </h2>
              {/* Only 'User' role can create new tickets */}
              {user.role === "User" && (
                <button
                  // Navigate to the create ticket page
                  onClick={() => router.push("/tickets/create")}
                  className="bg-blue-600 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded transition"
                >
                  Create New Ticket
                </button>
              )}
            </div>

            {/* Responsive Table Container */}
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-gray-200">
                {/* Table Header */}
                <thead className="bg-gray-50">
                  <tr>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      ID
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Category
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Title
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Status
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Priority
                    </th>
                    <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                      Date
                    </th>
                  </tr>
                </thead>
                {/* Table Body */}
                <tbody className="bg-white divide-y divide-gray-200">
                  {filteredTickets.length > 0 ? (
                    // Map through filtered tickets and render rows
                    filteredTickets.map((ticket) => (
                      <tr key={ticket.id}>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                          #{ticket.id}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                          {ticket.category || "General"}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                          {ticket.title}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm">
                          {/* Status Logic: Heads can edit, Users just view */}
                          {user.role === "Head" ? (
                            <select
                              value={ticket.status}
                              onChange={(e) =>
                                handleStatusChange(ticket.id, e.target.value)
                              }
                              // Conditional styling based on status value
                              className={`px-2 py-1 text-xs font-semibold rounded-full border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-500 ${
                                ticket.status === "Open"
                                  ? "bg-green-100 text-green-800"
                                  : ticket.status === "Closed"
                                    ? "bg-red-100 text-red-800"
                                    : "bg-gray-100 text-gray-800"
                              }`}
                            >
                              <option value="Open">Open</option>
                              <option value="In Progress">In Progress</option>
                              <option value="Closed">Closed</option>
                            </select>
                          ) : (
                            // Read-only status badge for regular users
                            <span
                              className={`px-2 inline-flex text-xs leading-5 font-semibold rounded-full ${
                                ticket.status === "Open"
                                  ? "bg-green-100 text-green-800"
                                  : ticket.status === "Closed"
                                    ? "bg-red-100 text-red-800"
                                    : "bg-gray-100 text-gray-800"
                              }`}
                            >
                              {ticket.status}
                            </span>
                          )}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                          {ticket.priority}
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                          {new Date(ticket.date).toLocaleDateString()}
                        </td>
                      </tr>
                    ))
                  ) : (
                    // Empty state row
                    <tr>
                      <td
                        className="px-6 py-4 whitespace-nowrap text-sm text-gray-500 text-center"
                        colSpan={6}
                      >
                        No tickets found.
                      </td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </main>
    </div>
  );
}
